import { Component, OnInit } from '@angular/core';
import { CALENDAR_CN } from '../../../credit-ccm/common/http/credit-ccm.http.constant';
import { Message } from 'primeng/primeng';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { TZB_CUS_DOWNLOAD } from '../../../../../../@uisftech/common/service/http.constant';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { QueryContactMarketingCampaignListBean } from './bean/staff-speed-track.bean';
import { ContactInformation, LinkageTheme } from '../../custom-constant/custom-code-value';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'staff-speed-track',
  templateUrl: './staff-speed-track.component.html',
  styleUrls: ['./staff-speed-track.component.scss']
})
export class StaffSpeedTrackComponent implements OnInit {

  deployTimeStart: Date = null;
  deployTimeEnd: Date = null;
  msgs: Message[] = [];
  planContactTimeStart: Date = null;
  planContactTimeEnd: Date = null;
  ch: any = CALENDAR_CN;
  mapList: any = []; //
  display: Boolean = false;
  showFlagFeild: Boolean = false; // 
  showFlagQuery: Boolean = false; // 
  title: string; // 
  hrOrgName: string; // 
  contactWay: string; // 
  tableMesg: string; // 
  total: number;
  first: number = 1;
  DOWNLOAD: string = TZB_CUS_DOWNLOAD;//
  pageSize: string; // 
  personPage: boolean = false;
  userInValue: any;
  userId: string; //  
  rzjg: string;//
  personName: string; // 
  orgInValue: any; // 
  orgTreeDisplay: boolean = false;//
  custMajorName: string = ''; // 
  contactInformation: any = ContactInformation // 
  linkageTheme: any = LinkageTheme; // 
  setInvalue: any; // 
  // allList: any; // 
  // selectedList: any;// 

  campaignListBean: QueryContactMarketingCampaignListBean = new QueryContactMarketingCampaignListBean();

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public datePipe: DatePipe,
  ) { }

  ngOnInit() {
    // // 
    // this.allList = this.setInvalue
    // // 
    // this.selectedList = this.setInvalue
    this.campaignListBean.pageSize = 10;
    this.campaignListBean.pageNum = 1;
    this.first = 1;
    // 
    this.queryCommunicationEventUserStatistics();
  }

  // 
  query() {
    this.campaignListBean.pageSize = 10;
    this.campaignListBean.pageNum = 1;
    this.first = 1;
    this.campaignListBean.pushDateStart = this.datePipe.transform(this.campaignListBean.pushDateStart, 'yyyy-MM-dd')
    this.campaignListBean.pushDateEnd = this.datePipe.transform(this.campaignListBean.pushDateEnd, 'yyyy-MM-dd')
    this.campaignListBean.fromDateEnd = this.datePipe.transform(this.campaignListBean.fromDateEnd, 'yyyy-MM-dd')
    this.campaignListBean.fromDateStart = this.datePipe.transform(this.campaignListBean.fromDateStart, 'yyyy-MM-dd')
    this.queryCommunicationEventUserStatistics();
  }

  // 
  idQuery() {
    this.display = true;
    this.showFlagQuery = true;
    this.showFlagFeild = false;
    this.title = '';
  }

  // 
  reset() {
    this.campaignListBean = new QueryContactMarketingCampaignListBean();
    this.custMajorName = '';
    this.personName = ''
    this.first = 0;
    this.query();
  }

  //
  chooseWorker() {
    document.body.style.overflow = 'hidden';
    this.personPage = !this.personPage;
    this.userInValue = this.rzjg;
  }

  //
  perInfor(item) {
    document.body.style.overflow = 'auto';
    this.personPage = false;
    if (item.length > 0) {
      this.campaignListBean.processUserId = item[0].tellerId; // 
      this.personName = item[0].tellerName; // 
    }
  }

  //
  chooseOrg() {
    document.body.style.overflow = 'hidden';
    this.orgInValue = 'noShow';
    this.orgTreeDisplay = !this.orgTreeDisplay;
  }

  //
  treeCall(param) {
    document.body.style.overflow = 'auto';
    this.orgTreeDisplay = false;
    this.custMajorName = param.orgName; // 
    this.campaignListBean.processOrgId = param.orgId;  //id
  }

  // 
  paginate(event) {
    this.campaignListBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.campaignListBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.campaignListBean.pageSize;
    this.queryCommunicationEventUserStatistics();
  }

  // 
  fieldSelect() {
    this.display = true;
    this.showFlagQuery = false;
    this.showFlagFeild = true;
    this.title = '';
  }

  // 
  queryCommunicationEventUserExport() {
    let param = {
      userName: JSON.parse(this.commfunc.getSessionDataCH('chName')).userName // 
    }
    this.httpService.queryCommunicationEventUserExport(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        window.open(this.DOWNLOAD + "?fileName=" + encodeURI(data.fileName) + "&fileUrl=" + data.fileUrl, "_self")
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }

  // 
  queryCommunicationEventUserStatistics() {
    this.campaignListBean.pushDateStart = this.datePipe.transform(this.campaignListBean.pushDateStart, 'yyyy-MM-dd');
    this.campaignListBean.pushDateEnd = this.datePipe.transform(this.campaignListBean.pushDateEnd, 'yyyy-MM-dd');
    let param = this.campaignListBean
    this.httpService.queryCommunicationEventUserStatistics(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        data.resultList.forEach(e => {
          e.processUserId = e.processUserId.substring(0, e.processUserId.indexOf("("));
        });
        this.mapList = data.resultList;
        console.log(this.mapList);
        this.total = data.totalNum
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }

  // 
  handInputDate(param1, param2) {
    this.showTime();
  }

  // 
  addUserDefinedSet(event) {
    this.display = event.display;
    if (event && event.selectedList && event.selectedList.length > 0) {
      this.setInvalue = event.selectedList;
    }
  }

  // 
  querySet(event) {
    this.campaignListBean.marketingCampaignId = event.list
    this.display = event.display
    this.showFlagQuery = event.display
  }



  // 
  showTime() {
    // let flag = this.commfunc.judgeexpireDate(this.contactList.contactTimeStart, this.contactList.contactTimeEnd)
    // console.log(flag)
    // if (flag) {
    //   this.msgs = [];
    //   this.msgs.push({ severity: 'error', summary: '', detail: '' });
    //   this.contactList.contactTimeEnd = null;
    // }
  }

}
