import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomContactTaskComponent } from './custom-contact-task.component';
import { RelaseSpeedTarckComponent } from './relase-speed-tarck/relase-speed-tarck.component';
import { CustomSpeedTrackComponent } from './custom-speed-track/custom-speed-track.component';
import { StaffSpeedTrackComponent } from './staff-speed-track/staff-speed-track.component';
import { ReleaseCustomContactTaskComponent } from './release-custom-contact-task/release-custom-contact-task.component';
//

export const routes: Routes = [
    {
        path: '',
        component: CustomContactTaskComponent,
        children: [
            { path: 'relase-speed-tarck', component: RelaseSpeedTarckComponent },//
            { path: 'release-custom-contact-task', component: ReleaseCustomContactTaskComponent },//
            { path: 'staff-speed-track', component: StaffSpeedTrackComponent },//
            { path: 'custom-speed-track', component: CustomSpeedTrackComponent },//
        ]
    },
]

export const CustomContactTaskRouting: ModuleWithProviders = RouterModule.forChild(routes);
